<script>
import { type } from "os";
export default {
  name: "KeyVisual",
  props: {
    msg: String
  },
  data() {
    return {
      name: "",
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      breakpoints: 3 /*xl:4 lg:3 md:2 sm:1 xs:0*/,
      kvlist: [
        { url: require("@/assets/kv/法台下雨.jpg"), caption: "5" },
        { url: require("@/assets/kv/住家前IMG_2824.jpg"), caption: "1" },
        { url: require("@/assets/kv/富士山_4034479.jpg"), caption: "2" },
        { url: require("@/assets/kv/日式房子IMG_2725.jpg"), caption: "3" },
        { url: require("@/assets/kv/日本橋IMG_2881.jpg"), caption: "4" }
      ],
      imagelist: {
        slogan: {
          pc: require("@/assets/svg/slogan.svg"),
          mobile: require("@/assets/svg/slogan-w.svg")
        },
        name: require("@/assets/svg/DekaEvelyn.svg"),
        stamp: require("@/assets/svg/de-stamp.svg"),
        info: require("@/assets/svg/info.svg"),
        goDown: require("@/assets/svg/down-icon.svg")
      }
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", vm.handleResize);
    console.log(window.screen.orientation.type);
    console.log(vm.fullHeight);
    console.log(vm.fullWidth);
    vm.checkBreakPoint();
  },
  computed: {
    userAgent() {
      let vm = this;
      return vm.$store.state.userAgent;
    }
  },
  mounted() {
    $(".goButton").click(() => {
      $("html,body").animate({ scrollTop: $("#presence").offset().top }, 500);
      return false;
    });
  },
  methods: {
    handleResize(event) {
      let vm = this;
      vm.checkBreakPoint();
    },
    checkBreakPoint() {
      let vm = this;
      vm.fullWidth = document.documentElement.clientWidth;
      vm.fullHeight = document.documentElement.clientHeight;
      if (vm.fullWidth >= 1200) {
        vm.breakpoints = 4;
      } else if (vm.fullWidth >= 992) {
        vm.breakpoints = 3;
      } else if (vm.fullWidth >= 768) {
        vm.breakpoints = 2;
      } else if (vm.fullWidt >= 576) {
        vm.breakpoints = 1;
      } else {
        vm.breakpoints = 0;
      }
    },
    computeWeddingDay() {
      var today = new Date();
      var weddingday = new Date("10/26/2019");
      var iDays = parseInt(Math.abs(weddingday - today) / 1000 / 60 / 60 / 24); // 把相差的毫秒數轉換為天數
      return iDays;
    }
  }
};
</script>

