<template>
  <div class="home" id="home">
    <KeyVisual msg="主視覺" />
    <!-- <Story msg="故事"/> -->
    <Gallery msg="相簿" />
    <WeddingPhoto msg="婚紗照全集" />
    <Traffic msg="交通" />
    <Attention msg="出席意願調查" />
    <Comment msg="留言" />
    <a href="javascript:void(0);" class="nav-icon top-icon">
      <img :src="imagelist.up_icon" alt />
    </a>
  </div>
</template>

<script>
// @ is an alias to /src
import KeyVisual from "@/components/KeyVisual.vue";
//import Story from "@/components/Story.vue";
import Gallery from "@/components/Gallery.vue";
import WeddingPhoto from "@/components/WeddingPhoto.vue";
import Traffic from "@/components/Traffic.vue";
import Attention from "@/components/Attention.vue";
import Comment from "@/components/Comment.vue";

export default {
  name: "home",
  components: {
    KeyVisual,
    //Story,
    Gallery,
    WeddingPhoto,
    Traffic,
    Attention,
    Comment
  },

  data() {
    return {
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      lastpageYOffset: 0,
      imagelist: {
        up_icon: require("@/assets/svg/up-icon.svg")
      }
    };
  },
  created() {
    let vm = this;
  },
  mounted() {
    let vm = this;
    if (vm._isMobile()) {
      console.log("set mobile");
      vm.$store.commit({
        type: "setUserAgent",
        userAgent: "mobile"
      });
    } else {
      console.log("set pc");
      vm.$store.commit({
        type: "setUserAgent",
        userAgent: "pc"
      });
    }
    $(".top-icon").click(() => {
      $("html,body").animate({ scrollTop: 0 }, 500);
      return false;
    });
  },
  methods: {
    handleScroll(event) {
      let vm = this;
      var scrollOffset = 10;
      //scroll down
      var diff = window.pageYOffset - vm.lastpageYOffset;
      if (diff > 0) {
        if (window.pageYOffset > scrollOffset) {
          var section = 1;
          window.scrollTo(0, vm.fullHeight * section);
        } else if (window.pageYOffset > vm.fullHeight + scrollOffset) {
          var section = 2;
        }
      }
      vm.lastpageYOffset = window.pageYOffset;
    },
    _isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      return flag;
    }
  }
};
</script>

