<template>
  <!-- Using components -->
  <div class="fluid-container WeddingPhoto">
    <img :src="title_svg" class="title_svg" />
    <b-container v-if="userAgent === 'pc'" class="p-4 d-flex align-items-center pc-part">
      <b-row>
        <b-col cols="3" class="mb-5 photogrid" v-for="item in photolist" :key="item.id">
          <b-img
            thumbnail
            fluid
            :src="item.url"
            :alt="item.alt"
            @click="setCategory(item.category)"
          ></b-img>
          <p class="caption">{{ item.caption }}</p>
        </b-col>
      </b-row>
    </b-container>
    <b-container v-if="userAgent === 'mobile'" class="p-4 d-flex align-items-center mobile-part">
      <b-row>
        <b-col cols="6" class="photogrid" v-for="item in photolist" :key="item.id">
          <b-img
            thumbnail
            fluid
            :src="item.url"
            :alt="item.alt"
            @click="setCategory(item.category)"
          ></b-img>
          <p class="caption">{{ item.caption }}</p>
        </b-col>
      </b-row>
    </b-container>
  </div>
  <!-- /container -->
</template>

<script>
import { type } from "os";

export default {
  name: "WeddingPhoto",
  props: {
    msg: String
  },
  data() {
    return {
      name: "",
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      breakpoints: 3 /*xl:4 lg:3 md:2 sm:1 xs:0*/,
      lastScrollTop: 0,
      title_svg: require("@/assets/svg/title-01.svg"),
      photolist: [
        {
          url: require("@/assets/weddingphoto/遊樂園.jpg"),
          category: 1,
          alt: "image",
          caption: "遊樂園"
        },
        {
          url: require("@/assets/weddingphoto/日本其他.jpg"),
          category: 2,
          alt: "image",
          caption: "日本箱根"
        },
        {
          url: require("@/assets/weddingphoto/宿霧.jpg"),
          category: 5,
          alt: "image",
          caption: "宿霧"
        },
        // {
        //   url: require("@/assets/weddingphoto/日本櫻花.jpg"),
        //   category: 4,
        //   alt: "image",
        //   caption: "日本櫻花"
        // },
        // {
        //   url: require("@/assets/weddingphoto/日本富士山.jpg"),
        //   category: 3,
        //   alt: "image",
        //   caption: "日本富士山"
        // },
        {
          url: require("@/assets/weddingphoto/大東.jpg"),
          category: 6,
          alt: "image",
          caption: "大東藝術中心"
        }
        // {
        //   url: "https://picsum.photos/400/400/?image=59",
        //   category: 7,
        //   alt: "image",
        //   caption: "求婚"
        // },
        // {
        //   url: require("@/assets/weddingphoto/中國風.jpg"),
        //   category: 8,
        //   alt: "image",
        //   caption: "搞怪系列"
        // }
      ]
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", vm.handleResize);
    vm.checkBreakPoint();
  },
  mounted() {},
  computed: {
    userAgent() {
      let vm = this;
      return vm.$store.state.userAgent;
    }
  },
  methods: {
    handleResize(event) {
      let vm = this;
      vm.checkBreakPoint();
    },
    checkBreakPoint() {
      let vm = this;
      vm.fullWidth = document.documentElement.clientWidth;
      vm.fullHeight = document.documentElement.clientHeight;
      if (vm.fullWidth >= 1200) {
        vm.breakpoints = 4;
      } else if (vm.fullWidth >= 992) {
        vm.breakpoints = 3;
      } else if (vm.fullWidth >= 768) {
        vm.breakpoints = 2;
      } else if (vm.fullWidt >= 576) {
        vm.breakpoints = 1;
      } else {
        vm.breakpoints = 0;
      }
    },
    setCategory(category) {
      let vm = this;
      vm.$store.commit({
        type: "setCategory",
        category: category
      });
      vm.$router.push({
        path: "weddingPhoto"
      });
    }
  }
};
</script>

