<template>
  <!-- Using components -->
  <div class="fluid-container comment">
    <img :src="title_svg" class="title_svg" />
    <div class="container">
      <div id="fb-root"></div>
      <div
        class="fb-comments"
        data-width
        data-href="https://wedding.dcstudio.nctu.me"
        data-numposts="10"
        data-order-by="reverse_time"
        data-mobile
      ></div>
    </div>
  </div>
  <!-- /container -->
</template>

<script>
import { type } from "os";

export default {
  name: "Comment",
  props: {
    msg: String
  },
  data() {
    return {
      name: "",
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      breakpoints: 3 /*xl:4 lg:3 md:2 sm:1 xs:0*/,
      lastScrollTop: 0,
      title_svg: require("@/assets/svg/title-04.svg")
    };
  },
  created() {
    let vm = this;
    window.addEventListener("resize", vm.handleResize);
    vm.checkBreakPoint();
    FB.XFBML.parse();
  },
  mounted() {},
  computed: {},
  methods: {
    handleResize(event) {
      let vm = this;
      vm.checkBreakPoint();
    },
    checkBreakPoint() {
      let vm = this;
      vm.fullWidth = document.documentElement.clientWidth;
      vm.fullHeight = document.documentElement.clientHeight;
      if (vm.fullWidth >= 1200) {
        vm.breakpoints = 4;
      } else if (vm.fullWidth >= 992) {
        vm.breakpoints = 3;
      } else if (vm.fullWidth >= 768) {
        vm.breakpoints = 2;
      } else if (vm.fullWidt >= 576) {
        vm.breakpoints = 1;
      } else {
        vm.breakpoints = 0;
      }
    }
  }
};
</script>

