<template>
  <div class="weddingPhoto">
    <WeddingPhotoPage msg="相簿" />
    <a href="javascript:void(0);" class="nav-icon home-icon">
      <img :src="imagelist.home_icon" alt />
    </a>
    <a href="javascript:void(0);" class="nav-icon top-icon">
      <img :src="imagelist.up_icon" alt />
    </a>
  </div>
</template>

<script>
// @ is an alias to /src
import WeddingPhotoPage from "@/components/WeddingPhotoPage.vue";

export default {
  name: "weddingPhoto",
  components: {
    WeddingPhotoPage
  },
  data() {
    return {
      fullWidth: document.documentElement.clientWidth,
      fullHeight: document.documentElement.clientHeight,
      lastpageYOffset: 0,
      imagelist: {
        up_icon: require("@/assets/svg/up-icon.svg"),
        home_icon: require("@/assets/svg/home-icon.svg")
      }
    };
  },
  computed: {},
  mounted() {
    $(".home-icon").click(() => {
      let vm = this;
      vm.goback();
      return false;
    });
    $(".top-icon").click(() => {
      $("html,body").animate({ scrollTop: 0 }, 500);
      return false;
    });
  },
  methods: {
    goback() {
      let vm = this;
      vm.$router.go(-1);
    }
  }
};
</script>

